import React, { Fragment } from 'react';
import ReactDOM from 'react-dom/client';
import {BrowserRouter,Routes,Route} from 'react-router-dom';
import './index.css';
import {Header , Projects, Contacts, Blog, Resume, Title, Skills, About} from './App';
import ParticleBg from './TsParticle'
import reportWebVitals from './reportWebVitals';

const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(
  <BrowserRouter basename={process.env.PUBLIC_URL}>
    
    <Routes>
      <Route path='/' element={
      <React.Fragment>
        <ParticleBg className="Particles" />
        <section className='TopPage'>
          <Header/>
          <Title />
        </section>
        <About />
        <Resume />
        <Skills />
        <Projects />
        <Contacts />
      </React.Fragment>
      }/>

      <Route  path='/projects' element={
        <React.Fragment>
          <Header />
          <Projects />
        </React.Fragment>
      } 
      />

      <Route  path='/contacts' element={
        <React.Fragment>
          <Header />
          <Contacts />
        </React.Fragment>
      } 
      />

      <Route path='/blog' element={
        <React.Fragment>
          <Header />
          <Blog />
        </React.Fragment>
      }
      />

      <Route path='/resume' element={
        <React.Fragment>
          <Header />
          <Resume />
        </React.Fragment>
      }
      />
    </Routes>
  </BrowserRouter>
);
reportWebVitals();
